<template>
  <div class="search">
    <div class="menu-se clearfix">
      <div class="scan fl">
        <img
          src="../assets/images/left.png"
          alt
          class="auto-img"
          @click="goback"
        />
      </div>
      <div class="menu-search fl">
        <van-search
          v-model="text"
          placeholder="请输入搜索关键词"
          input-align="center"
          shape="round"
          clearable
          @input="inputSearch"
        />
        <div class="search-icon">
          <img src="../assets/images/12.png" alt class="auto-img" />
        </div>
      </div>
      <div class="vedio fr" @click="search">搜索</div>
    </div>

    <div class="no-search" v-show="isshow">
      <div class="kong">
        <img src="../assets/images/kong.png" alt="" class="auto-img" />
      </div>
      <div class="seaech-retui">今日搜索推荐</div>
        <el-backtop></el-backtop>
        <div class="rank" v-for="(item, index) in renew" :key="index">
          <div class="rank-list">
            <div class="rank-list-left">
              <img :src="item.cover" alt="" class="auto-img" />
            </div>
            <div class="rank-list-right">
              <div class="rank-name">{{ item.name }}</div>
              <div class="rank-author">最新：{{ item.latest }}</div>
              <div class="rank-text">
                <div class="text-time fl">{{ item.time }}</div>
                <div class="text-icon fr" @click="recommend(item.name)">
                  <img
                    src="../assets/images/topgo.png"
                    alt=""
                    class="auto-img book-icon"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
    </div>

    <div class="rank" v-for="(item, index) in searchList" :key="index" v-show="searchList">
      <el-backtop></el-backtop>
      <div class="rank-list">
        <div class="rank-list-left">
          <img :src="item.cover" alt="" class="auto-img" />
        </div>
        <div class="rank-list-right">
          <div class="rank-name">{{ item.name }}</div>
          <div class="rank-author">更新至：{{ item.latest }}</div>
          <div class="rank-text clearfix">
            <div class="text-time fl">{{ item.tag }}</div>
            <div class="text-icon fr" @click="goDetail(item.url)">
              <img
                src="../assets/images/yuedu.png"
                alt=""
                class="auto-img book-icon"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/search.less";
export default {
  data() {
    return {
      // 关键字
      text: "",
      // 搜索列表
      searchList: [],
      isshow: true,
      //搜索推荐
      renew:[],
      //搜索推荐关键字
      name:""
    };
  },
  created() {
    //搜索推荐
    this.referral();
  },
  methods: {
    //回退
    goback() {
      this.isshow=true;
      this.text="";
       // 搜索列表
      this.searchList=[],
      this.$router.push({ name: "Home" });
    },

    //监听
     inputSearch(){
      if(this.text==""){
        this.isshow=true;
      this.searchList= []
      }
    },

    //搜索推荐
    referral(){
       this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhlb: "new",
        },
      })
        .then((res) => {
          let resList = res.data.list;
          resList.shift();
          this.renew = resList.splice(10, 20);
        })
        .catch(() => {});
    },

    // 搜索显示
    recommend(name){
      this.text=name;
    },
    //搜索
    search() {
      if (this.text == "") {
        this.$notify({ background: "#157efb", message: "请输入关键字" });
        return;
      }
      this.isshow=false;
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhname: this.text,
        },
      })
        .then((res) => {
          this.$toast.clear();
          this.searchList = res.data.list;
        })
        .catch(() => {
          this.$toast.clear();
        });
    },
    // 查看详情
    goDetail(url) {
      this.$router.push({
        name: "Detail",
        query: {
          bookurl: url,
          tag: "search",
        },
      });
    },
  },
};
</script>
<style lang="less" scoped>
</style>